body {
  margin: 0 !important;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, "Lexend Deca";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.nav-link{
  text-decoration: none;
  width: 100%;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  grid-gap: 50px;
  gap: 50px;
  height: 100%;
  width: 100%;
  margin: 0px auto;
}
.App {
  height: 100%;
  width: 100%;
  position: absolute;
  justify-content: center;
  align-items: center;
  margin-top: -20px;
}
.topButtons{
  display: flex;
  justify-content: space-between;  
  position: absolute;
  width: 90%;
  z-index: 99;
  padding: 5%;
}

.fullScreenBtn{
  margin-left: auto;
}

#roundBtn {
  display: flex;
  background-color: #6FAFC9;
  border: none;
  color: white;
  padding: 15px;
  font-size: 20px;
  border-radius: 50%;
  height: 100%;
}

.svg-inline--fa {
  width: 1em;
}
.textButton {
  width: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
}

#textBtn {
  border: none;
  color: #121212;
  padding: 15px;
  font-size: 22px;
  border-radius: 35px;
  width: 80%;
  background-color: #E2E2E2;
  font-family: "Lexend Deca";
}
.Home {
  background-color: white;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.menu{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  grid-gap: 10px;
  gap: 10px;
  width: 100%;
}

.logo{
  max-width: 70%;
  max-height: 30%;
}
.RealidadeVirtual {
  background-color: white;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.RealidadeAumentada {
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}

.alignElements{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}

.alignImage{
    display: block;
    align-self: center;
    opacity: 0.7;
    max-width: 100%;
    max-height: 100%;
}

.alignedBtn{
    position: absolute;
    bottom: 10px;
    display: flex;
    background-color: #12121298;
    border: none;
    color: white;
    padding: 15px;
    font-size: 20px;
    border-radius: 10%;
    z-index: 99;
    font-family: "Lucida Sans";
}
